.icon-left {
    position: absolute;
    top : 10px;
    left: 10px;

}

.icon-right{
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.season-display.winter i{
    color:blue;
}

.season-display.sumer i{
    color:coral;
}

.season-display {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.winter {
    background-color: aliceblue;
}

.sumer {
    background-color: coral;
}